const INITIAL_STATE ={
  articles:[]
}


function articleReducer(state=INITIAL_STATE,action){

  switch(action.type){
    

    case "LOADARTICLES":{
      return{
        ...state,
        articles: action.payload
        
      }
    }
    default:
    
  }
  return state
}
export default articleReducer


export const getArticles=()=>dispatch =>{

  // fetch('https://jsonplaceholder.typicode.com/posts')
 // fetch('http://localhost/serveurd_facilities/front/articles')
  fetch('https://cindy-ruet.fr/dfacilities/serveurd_facilities/index.php?page=front/articles')
  .then(response => response.json())
  .then(data =>{
    dispatch({
        type:'LOADARTICLES',
        payload: data
    })
    
  })
}