const INITIAL_STATE ={
  equipe:[]
}


function equipeReducer(state=INITIAL_STATE,action){

  switch(action.type){
    

    case "LOADEQUIPE":{
      return{
        ...state,
        equipe: action.payload
        
      }
    }
    default:
    
  }
  return state
}
export default equipeReducer


export const getEquipe=()=>dispatch =>{

  // fetch('https://jsonplaceholder.typicode.com/posts')
  //fetch('http://localhost/serveurd_facilities/front/equipe')
  fetch('https://cindy-ruet.fr/dfacilities/serveurd_facilities/index.php?page=front/equipe')
  .then(response => response.json())
  .then(data =>{
    dispatch({
        type:'LOADEQUIPE',
        payload: data
    })
    
  })
}