const INITIAL_STATE ={
  offres:[]
}


function offreReducer(state=INITIAL_STATE,action){

  switch(action.type){
   
    case "LOADOFFRES":{
      return{
        ...state,
        offres: action.payload        
      }
    }
    default:   
  }
  return state
}
export default offreReducer


export const getOffres=()=>dispatch =>{

  // fetch('https://jsonplaceholder.typicode.com/posts')
  //fetch('http://localhost/serveurd_facilities/front/offres')
 fetch('https://cindy-ruet.fr/dfacilities/serveurd_facilities/index.php?page=front/offres')
  .then(response => response.json())
  .then(data =>{
    dispatch({
        type:'LOADOFFRES',
        payload: data
    })
    
  })
}